body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*  App-Wide styles for components. Can be overridden by child CSS classes. */
/*  Type-Specific Styling */
input {
  border: 0;
  width: 100%;
  padding: 12px;
  border-radius: 10px;
  box-sizing: border-box; }

input[type=submit] {
  color: white;
  background-color: darkslategray;
  transition: .25s; }

input[type=text] {
  color: black; }

input[type=submit]:hover {
  color: white;
  background-color: green;
  transition: .25s; }

.background-particles {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  z-index: -1337;
  color: grey;
  background-color: slategray; }

/*  Component-Specific Styling */
.App {
  text-align: center;
  color: white; }

.App-logo {
  height: 40vmin;
  max-width: 200px; }

.App-header {
  display: -webkit-flex;
  display: flex;
  min-height: 10vh;
  height: 7.5vh;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  background-color: #252525;
  color: #d1d1d1; }

.App-body {
  display: grid;
  margin: auto;
  -webkit-align-items: center;
          align-items: center;
  max-width: 400px;
  width: 85vw; }

.body-text {
  display: grid;
  max-width: 400px;
  width: 85vw; }

.user-form {
  display: grid;
  max-width: 400px;
  width: 85vw; }

.fill-style {
  width: -webkit-fill-available; }

.login {
  background-color: #f7f5f5;
  padding: 20px;
  height: 350px;
  width: 450px;
  text-align: center;
  line-height: 50px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2); }

.login h1 {
  font-style: oblique;
  color: #464545; }

.login h4 {
  font-style: oblique;
  color: #696868; }

